"use client";
import React, { useState, useEffect, useRef } from 'react'
import Image from 'next/image'

function Section05() {
    const [isVisible, setIsVisible] = useState(false);
    const sectionRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        setIsVisible(true);
                    }
                });
            },
            {
                threshold: 0.1,
                rootMargin: '0px 0px -50px 0px'
            }
        );

        if (sectionRef.current) {
            observer.observe(sectionRef.current);
        }

        return () => {
            if (sectionRef.current) {
                observer.unobserve(sectionRef.current);
            }
        };
    }, []);

    return (
        <div className='grid-container' ref={sectionRef}>
            <div className='grid lg:grid-cols-2 gap-4 items-stretch'>
                <div className={`flex flex-col mb-4 lg:mb-8 transition-opacity duration-[2000ms] ${isVisible ? 'animate-fade-in-up opacity-100' : 'opacity-0'}`}>
                    <h2 className='title  encode-sans-medium bg-gradient-to-r from-[#191919] to-[#919191] bg-clip-text text-transparent leading-none transition-all duration-[1500ms]'>
                        PURE SRI LANKAN FROM THE SOURC
                    </h2>
                    <div 
                        className={`bg-black w-3/6 h-[2px] mt-4 mb-4 transition-all duration-[1500ms] ${isVisible ? 'animate-fade-in opacity-100' : 'opacity-0'}`}
                        style={isVisible ? { animationDelay: '0.6s', animationFillMode: 'both' } : {}}
                    ></div>
                    <p 
                        className={`description encode-sans-medium text-gray-500 leading-none transition-all duration-[1500ms] ${isVisible ? 'animate-fade-in opacity-100' : 'opacity-0'}`}
                        style={isVisible ? { animationDelay: '0.9s', animationFillMode: 'both' } : {}}
                    >
                    Harvested from fertile lands and carefully processed to preserve authenticity, our spices bring tradition, quality, and excellence to every kitchen.Harvested from fertile lands and carefully processed to preserve authenticity, our spices bring tradition, quality, and excellence to every kitchen.
                    </p>
                </div>
                <div className='h-[200px] lg:h-full'>
                    <div className='grid grid-cols-5 gap-4 h-full'>
                        <div className='col-span-3 h-full overflow-hidden rounded-apple-image'>
                            <div className='relative w-full h-full transition-transform duration-700 hover:scale-110'>
                                <Image
                                    src="/Images/Home/Section02/Middleimage.png"
                                    alt="Middle Image"
                                    fill
                                    className={`object-cover rounded-apple-image transition-all duration-[1500ms] hover:brightness-110 ${isVisible ? 'animate-fade-in opacity-100' : 'opacity-0'}`}
                                    style={isVisible ? { animationDelay: '1.2s', animationFillMode: 'both' } : {}}
                                />
                            </div>
                        </div>
                        <div className='col-span-2 h-full overflow-hidden rounded-apple-image'>
                            <div className='relative w-full h-full transition-transform duration-700 hover:scale-110'>
                                <Image
                                    src="/Images/Home/Section02/Middleimage.png"
                                    alt="Middle Image"
                                    fill
                                    className={`object-cover rounded-apple-image transition-all duration-[1500ms] hover:brightness-110 ${isVisible ? 'animate-fade-in opacity-100' : 'opacity-0'}`}
                                    style={isVisible ? { animationDelay: '1.5s', animationFillMode: 'both' } : {}}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Section05